*::before,
*::after {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.preciptype {
  font-size: 3rem;
}

.search {
  padding-top: 1rem;
  justify-self: center;
  align-self: center;
  input[type="radio"] {
    margin-right: 0.1rem;
  }
  p {
    padding-top: 0.5rem;
    justify-self: center;
  }
}



.day-cards {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  padding: 1rem;
}

.day-card {
  border-radius: 5px;
  border: 1px solid gray;
  box-shadow: 1px 1px 10px darkgray;
  padding: 1rem;
}

#content {
  padding: 1rem;
  h1{ 
    justify-self: center;
  }
